@charset "UTF-8";

// Default Variables

// Slick icon entity codes outputs the following
// "\2190" outputs ascii character "←"
// "\2192" outputs ascii character "→"
// "\2022" outputs ascii character "•"

$slick-font-path: "./fonts/" !default;
$slick-font-family: "slick" !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: white !default;
$slick-dot-color: black !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-prev-character: "\2190" !default;
$slick-next-character: "\2192" !default;
$slick-dot-character: "\2022" !default;
$slick-dot-size: 6px !default;
$slick-opacity-default: 1 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

@function slick-image-url($url) {
    @if function-exists(image-url) {
        @return image-url($url);
    }
    @else {
        @return url($slick-loader-path + $url);
    }
}

@function slick-font-url($url) {
    @if function-exists(font-url) {
        @return font-url($url);
    }
    @else {
        @return url($slick-font-path + $url);
    }
}

/* Slider */

.slick-list {
    .slick-loading & {
        background: #fff slick-image-url("../img/ajax-loader.gif") center center no-repeat;
    }
}

/* Icons */
@if $slick-font-family == "slick" {
    @font-face {
        font-family: "slick";
        src: slick-font-url("slick.eot");
        src: slick-font-url("slick.eot?#iefix") format("embedded-opentype"), slick-font-url("slick.woff") format("woff"), slick-font-url("slick.ttf") format("truetype"), slick-font-url("slick.svg#slick") format("svg");
        font-weight: normal;
        font-style: normal;
    }
}

/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    z-index: 11;
    display: block;
    cursor: pointer;
    background: #fff;
    color: transparent;
    top: 40%;
    box-shadow: 0 0 3px 1px rgba(0, 0, 6, 0.2);
    width: 40px;
    height: 40px;
    padding: 0;
    border: none;
    outline: none;
    &:after {
        content: '';
        display: block;
        position: absolute;
        left: 16px;
        top: 13px;
        width: 15px;
        height: 15px;
        border-top: 1px solid #000;
        border-right: 1px solid #000;
        transform: rotate(-135deg);
        transition: all 200ms linear;
    }
    &:hover, &:focus {
        outline: none;
        &:before {
            opacity: $slick-opacity-on-hover;
        }
    }
    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }
    &:before {
        display: inline-block;
        vertical-align: top;
        font-size: em(56px, 30px);
        line-height: 1;
        color: #fff;
        opacity: $slick-opacity-default;
        text-shadow: 1px 0 0 rgba(0, 0, 0, 0.3);
    }
}

.slick-gallery {
    .slick-prev,
    .slick-next {
        opacity: 0;
        top: 50%;
        margin-top: -20px;
        transition: opacity .2s ease;
    }
    &:hover {
        .slick-prev,
        .slick-next {
            opacity: 0.7;
        }
    }
}

@keyframes left-anim {
    0% {
        margin-left: 0;
    }
    50% {
        margin-left: -5px;
    }
    100% {
        margin-left: 0;
    }
}
@keyframes right-anim {
    0% {
        margin-left: 0;
    }
    50% {
        margin-left: 5px;
    }
    100% {
        margin-left: 0;
    }
}

.slick-prev {
    left: 0;
    &:hover {
        &:after {
            animation: left-anim 2s infinite;
        }
    }
}

.slick-next {
    right: 0;
    &:after {
        left: 10px;
        transform: rotate(45deg);
    }
    &:hover {
        &:after {
            animation: right-anim 2s infinite;
        }
    }
}

/* Dots */

.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -35px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 12px;
        width: 12px;
        margin: 0 6px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            display: block;
            width: 100%;
            height: 100%;
            background-color: #b0b0b0;
            border-radius: 50%;
            cursor: pointer;
            position: relative;
            transition: background-color .3s ease-out;

            &:after {
                content: "";
                position: absolute;
                top: -4px;
                left: -4px;
                bottom:-4px;
                right:-4px;
                border: 1px solid #d83666;
                transform: scale3d(0.6, 0.6, 0.6);
                border-radius: inherit;
                transition: all .4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
                opacity: 0;
            }
            &:hover, &:focus {
                outline: none;
            }
        }

        &.slick-active {
            button {
                background-color: #d83666;
                &:after {
                    opacity: 1;
                    transform: scale3d(1, 1, 1);
                }
            }
        }
    }
}
